<template>
  <div class="answer-container">
    <div class="question-container">
      <div v-for="(item,itemIndex) of questions" class="question-item"
           v-show="itemIndex>=startIndex&&itemIndex<endIndex"
           :key="`q_${item.id}`">
        <h3>{{item.title}}({{userAnswer(item)}})</h3>
        <div v-for="(answer,index) of item.choice" class="answer-item"
             @click="handleChoice(item,index)"
             :key="`q_a_${item.id}_${index}`">{{answer}}
        </div>
      </div>
      <div style="display: flex;justify-content: center;padding-top: 20px">
        <button class="next-btn" @click="handleNext"></button>
      </div>
    </div>
    <div v-show="showDialog" class="tina-dialog">
      <div class="wrapper">
        <div v-show="showErrorDialog" class="thank-join">
          <div class="tina-title">
            <img alt="" class="title-img" src="../assets/answer/thank_join.png">
          </div>
          <div class="tina-content">
            <p style="flex: 1">
              活动尚未结束，老铁还需努力，好运！
            </p>
            <div style="padding-right: 20px">
              <button @click="handleErrorBtn" class="confirm-btn">确定</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {QUESTIONS, USER_AN} from '../data/const'

  export default {
    name: "answer",
    data() {
      return {
        questions: [].concat(QUESTIONS),
        pageSize: 3,
        current: 1,
        showErrorDialog: false,
        showDialog: false
      }
    },
    methods: {
      userAnswer(item) {
        return USER_AN[item.userA]
      },
      handleChoice(item, index) {
        item.userA = index + 1;
      },
      handleNext() {
        let isAll = true;
        let maxIndex = this.endIndex;
        if (maxIndex > this.questions.length) {
          maxIndex = this.questions.length;
        }
        for (let index = 0; index < maxIndex; index++) {
          let question = this.questions[index];
          isAll = isAll && question.userA;
        }
        if (!isAll) {
          alert("请完成本页题目！");
          return;
        }
        if (this.current === 1) {
          this.current = 2;
        } else if (this.current === 2) {
          let allCorrect = true;
          this.questions.forEach(item => {
            allCorrect = allCorrect && (item.userA === item.answer)
          });
          if (!allCorrect) {
            this.showDialog = true;
            this.showErrorDialog = true;
          } else {
            this.$emit('next')
          }
        }
      },
      handleErrorBtn() {
        this.showErrorDialog = false;
        this.showDialog = false;
        this.reset();
      },
      reset() {
        console.log('question page=> reset');
        this.questions.forEach(item => {
          item.userA = 0;
        });
        this.current = 1;
      }
    },
    computed: {
      startIndex() {
        return this.pageSize * (this.current - 1)
      },
      endIndex() {
        return this.pageSize * this.current
      }
    }
  }
</script>

<style scoped>
  .answer-container {
    height: 100%;
    background-image: url("../assets/answer/bg.png");
    background-size: 100% 100%;
    position: relative;
  }

  .question-container {
    background-image: url("../assets/answer/question_bg.png");
    background-size: 100% 100%;
    position: absolute;
    bottom: 15px;
    left: 15px;
    right: 15px;
    min-height: 450px;
    max-height: 70%;
    box-sizing: border-box;
    padding: 10% 30px 15px;
  }

  .question-container .question-item {
    padding-top: 20px;
  }

  .question-item h3 {
    display: block;
    color: white;
    font-size: 90%;
  }

  .question-item .answer-item {
    color: white;
    font-size: 85%;
    padding-top: 3px;
  }

  .next-btn {
    height: 30px;
    width: 110px;
    background-image: url("../assets/answer/next_btn.png");
    background-size: 100% 100%;
    margin: 0 auto;
  }


</style>



