<template>
  <div class="game-container">
    <lucky-wheel class="wheel" @over="gameOver"/>
    <tina-dialog :show-dialog.sync="showDialog">
      <template slot="title">
        <span @click="reset" class="dialog-close-btn">x</span>
      </template>
      <div class="game-dialog-container">
        <h3 style="font-size: 1rem;color: yellow">恭喜你中奖了！</h3>
        <img alt="" src="../assets/game/prize_img.png">
        <div>
          <input v-model="info.name" class="form-input" placeholder="姓名">
          <input v-model="info.cardNumber" class="form-input" placeholder="身份证号" type="number">
          <textarea v-model="info.remark" class="form-input form-text" placeholder="单位、联系方式"></textarea>
        </div>
      </div>
      <button class="submit-btn" @click="handleSubmit">立即领奖</button>
    </tina-dialog>
  </div>
</template>

<script>
  import LuckyWheel from "../components/LuckyWheel/LuckyWheel";
  import TinaDialog from "../components/Dialog/TinaDialog";

  export default {
    name: "game",
    components: {TinaDialog, LuckyWheel},
    data() {
      return {
        showDialog: false,
        info: {
          name: '',
          cardNumber: '',
          remark: ''
        }
      }
    },
    methods: {
      gameOver(item) {
        console.log('gameOver=>', item);
        if (item.isPrize) {
          this.showDialog = true;
        } else {
          this.$emit('noPrize')
        }
      },
      handleSubmit() {
        this.showDialog = false;
        let formInfo = this.info;
        console.log('handleSubmit: ',formInfo)
      },
      reset(){
        this.info={};
        this.$emit('reset');
      }
    }
  }
</script>

<style scoped>
  .game-container {
    height: 100%;
    width: 100%;
    overflow: hidden;
    background-image: url("../assets/game/game_bg.jpg");
    background-repeat: no-repeat;
    background-size: 100%;
    position: relative;
  }

  .game-container .wheel {
    margin: 40% auto auto;
  }

  .game-container >>> .thank-join .tina-title {
    justify-content: flex-end;
    padding-right: 15px;
  }

  .game-container >>> .tina-dialog .thank-join .tina-content {
    background-color: transparent;
    width: 90%;
    margin: 0 auto;
    border-radius: 10px;
    padding: 0;
  }

  .game-container .dialog-close-btn {
    width: 20px;
    height: 20px;
    border-radius: 100%;
    border: solid 1px white;
    color: white;
  }

  .game-dialog-container {
    background: red;
    border-radius: 10px;
    padding: 15px;
  }

  .game-dialog-container img {
    width: 50%;
    display: block;
    margin: 0 auto;
  }

  .game-container .submit-btn {
    background-color: darkorange;
    border-radius: 15px;
    height: 30px;
    line-height: 30px;
    width: 150px;
    margin: 20px auto 0;
    max-width: 50%;
    font-size: 0.8rem;
  }

  .game-container .form-input {
    margin-top: 5px;
    width: 100%;
    font-size: 0.8rem;
    border-radius: 5px;
    border: none;
    padding: 2px 10px;
    box-sizing: border-box;
    line-height: 28px;
  }

  .game-container .form-text {
    height: 40px;
  }


</style>



