<template>
  <div id="app">
    <answer ref="question" v-show="page===1" @next="toNext" ></answer>
    <game v-show="page===2" @noPrize="showNoPrizeHint" @reset="handleErrorBtn"/>
    <tina-dialog :show-dialog.sync="showNoPrizeDialog" class="app-dialog">
      <template slot="title">
        <img alt="" class="title-img" src="./assets/answer/thank_join.png">
      </template>
      <p style="flex: 1">
        活动尚未结束，老铁还需努力，好运！
      </p>
      <div style="padding-right: 20px">
        <button @click="handleErrorBtn" class="confirm-btn">确定</button>
      </div>
    </tina-dialog>
  </div>
</template>

<script>
  import './styles/index.css'
  import './styles/common.css'
  import Answer from "./pages/answer";
  import Game from "./pages/game";
  import TinaDialog from "./components/Dialog/TinaDialog";

  export default {
    name: 'app',
    components: {
      TinaDialog,
      Game,
      Answer
    },
    data() {
      return {
        page: 1,
        showNoPrizeDialog: false
      }
    },
    methods: {
      toNext() {
        this.page = 2;
      },
      showNoPrizeHint() {
        this.showNoPrizeDialog = true;
        console.log('showNoPrizeHint=>')
      },
      handleErrorBtn() {
        this.$refs.question.reset();
        this.page  = 1;
        this.showNoPrizeDialog = false;
      }

    }
  }
</script>

<style>
  .app-dialog .title-img{
    width: auto;
    height: 60px;
  }

  .app-dialog  .confirm-btn{
    float: right;
    color: dodgerblue;
    border: none;
  }

</style>



